import React from 'react';
import styled from 'styled-components';

const ChartWrapper = styled.div`
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
`;

const ChartContainer = ({ children, height }) => {
  return (
    <ChartWrapper style={{ height }}>
      {children}
    </ChartWrapper>
  );
};

export default ChartContainer; 